<script>
  export default {
    name: "Home",
    data:()=>({
      rates_arr:null,
      loading:false,
      TWD_to_exrate:1,
      exrate_to_TWD:1,
    }),
    methods:{
      slick_rates(){
        setTimeout(()=>{
          $('.home-rate-Slick').slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: true,
            rows: 0,
            prevArrow: '<div class="slider-arrow arrow-prev"><i class="fas fa-angle-left"></i></div>',
            nextArrow: '<div class="slider-arrow arrow-next"><i class="fas fa-angle-right"></i></div>',
            customPaging : function(slider, i) {
                return '<span class="dots"></span>';
            },
          })
       },100)
      }
    },
    computed:{
      hot_rates(){
        return this.rates_arr
      },
    },
    mounted(){
       this.axios
        .get("https://global-currency.herokuapp.com/currency")
        .then(({ data }) => {
          this.rates_arr = data;
        })
        .then(()=>{
          this.$nextTick(function(){
            this.loading=true
            this.slick_rates();        
          })
        })
        .catch(err => {
          console.log(err);
        });
    },
  };
</script>

